namespace $ { export class $mol_portion_indicator extends $mol_view {

	/// widthStyle \0
	widthStyle() {
		return "0"
	}

	/// style * width <= widthStyle
	style() {
		return ( { ...super.style() , 
			"width" :  <any> this.widthStyle() ,
		} )
	}

} }

namespace $ { export class $mol_portion extends $mol_view {

	/// portion 0
	portion() {
		return 0
	}

	/// indWidthStyle \0
	indWidthStyle() {
		return "0"
	}

	/// indicator $mol_portion_indicator widthStyle <= indWidthStyle
	@ $mol_mem()
	indicator() {
		return new $mol_portion_indicator().setup( obj => { 
			obj.widthStyle = () => this.indWidthStyle()
		} )
	}

	/// sub / <= indicator
	sub() {
		return [].concat( this.indicator() )
	}

} }

