namespace $ { export class $mol_stack extends $mol_view {

	/// side false
	side() {
		return false
	}

	/// attr * mol_stack_side <= side
	attr() {
		return ( { ...super.attr() , 
			"mol_stack_side" :  <any> this.side() ,
		} )
	}

	/// main /
	main() {
		return [] as any[]
	}

	/// Main $mol_view sub <= main
	@ $mol_mem()
	Main() {
		return new $mol_view().setup( obj => { 
			obj.sub = () => this.main()
		} )
	}

	/// addon /
	addon() {
		return [] as any[]
	}

	/// Addon $mol_view sub <= addon
	@ $mol_mem()
	Addon() {
		return new $mol_view().setup( obj => { 
			obj.sub = () => this.addon()
		} )
	}

	/// sub / 
	/// 	<= Main 
	/// 	<= Addon
	sub() {
		return [].concat( this.Main() , this.Addon() )
	}

} }

